import { EmailPdf } from './../interface/email';
import { Validate } from 'is-valid-value';
import { EmailService } from './../serviciosCall/email.service';
import { environment } from './../../environments/environment.prod';
import { Component, OnInit } from '@angular/core';
import { Disparador, Scroll } from '../interface/menu';


@Component({
  selector: 'app-trabaja-nosotros',
  templateUrl: './trabaja-nosotros.component.html',
  styleUrls: ['./trabaja-nosotros.component.css']
})
export class TrabajaNosotrosComponent implements OnInit {
  public inputFileMessage = "Suba su cv pdf"
  public showLoading = false;
  public showMessage = false;
  public icono  ="";
  public mensaje  ="";
  public formulario = { 
    Nombre: '',
    Correo: '',
    File : ''
  }
  private mensajeError = "";
  private tipoValidacion = {
    _TEXT : "TEXT",
    _NUMBER : "NUMBER",
    _EMAIL : "EMAIL",
    _EMPTY : "EMPTY",
  }  

  private mensjaes = [{
    tipo: this.tipoValidacion._TEXT,
    mensaje : `Solos se permiten letras`
  },

  {
    tipo: this.tipoValidacion._NUMBER,
    mensaje : `Solos se permiten numeros`
  },

  {
    tipo: this.tipoValidacion._EMAIL,
    mensaje : `No es un email valido`
  },
  {
    tipo: this.tipoValidacion._EMPTY,
    mensaje : `Espacios en blancos`
  },

]
  private inputFile;
  private formValidado = false;
  public direccionSeleccion = environment.endpointImagenesSeleccion;
  constructor(
    private emailServ : EmailService
  ) { 

    Scroll.move();
  }

  ngOnInit() {
  }

  ngAfterViewChecked() {
    Disparador.OnChangeComponent.emit();
  }

  async ChangeFile(event)
  {
    this.inputFileMessage =  event.target.files[0].name
    this.inputFile = event.target.files[0];
    const valida  = /[.pdf]$/
    if(valida.test(this.inputFileMessage))
    { 
      let error = document.getElementById("error");
      error.style.visibility = "hidden"
      this.formValidado = true;
      
    }else{
      let error = document.getElementById("error");
      this.formValidado = false;
      error.style.visibility = "visible"
    }
    
  }

  async SendPdf()
  {
    if(this.formValidado)
    {                
      if(this.formulario.Correo != "" && this.formulario.Nombre != "" && this.inputFileMessage != "Suba su cv pdf")
      {
        const base64File:any = await this.toBase64(this.inputFile)    
        let objetoPdf = new EmailPdf();
        objetoPdf.pdf = base64File;
        objetoPdf.nombre = this.formulario.Nombre;
        objetoPdf.correo = this.formulario.Correo;
        
        this.showLoading = true;
        this.emailServ.sendEmailPdf(objetoPdf).subscribe((x:any)=>{
            this.showLoading = false;
            console.log(x);          
            if(x.enviado)
            {
              this.icono = "mail_outline";
              this.mensaje = "Â¡Mensaje Enviado!"
              this.showMessage = true
              setTimeout(()=>this.showMessage=false, 1200);
              this.ClearForm();
            }else{
              this.icono = "close";
              this.mensaje = "Â¡Mensaje No Enviado!"
              this.showMessage = true
              setTimeout(()=>this.showMessage=false, 12000);
            }
          },error=>{
            this.showLoading = false;
            console.log(error);
            this.icono = "close";
            this.mensaje = "Â¡Mensaje No Enviado!"
            this.showMessage = true
            setTimeout(()=>this.showMessage=false, 12000);
          }) 
        }      
    }
    
  }

  ClearForm()
  {
    this.formulario.Correo = "" ;
    this.formulario.Nombre = "";
    this.inputFileMessage = "Suba su cv pdf" ;
    this.formulario.File = "";
  }

  toBase64(input)
  {
    return new Promise((resolve, reject)=>{
      let fileReader = new FileReader();
      fileReader.readAsDataURL(input);
      fileReader.onload = ()=>{
        resolve(fileReader.result)
      }            

      fileReader.onerror = (error)=>{
        reject(error)
      }            
    })
  }

  Validacion(type:string, value:string)
  {
    let retorno = true
    switch(type.toUpperCase())
    {
      case this.tipoValidacion._TEXT: 
        retorno = this.ProcesoValidaciÃ³n(/^[a-zA-Z]+$/, value,this.tipoValidacion._TEXT)
      break;

      case this.tipoValidacion._NUMBER:
        retorno = this.ProcesoValidaciÃ³n(/^[0-9]+$/, value,this.tipoValidacion._NUMBER)
      break;

      case this.tipoValidacion._EMAIL: 
      retorno = this.ProcesoValidaciÃ³n(/[@][a-zA-Z]+[.]/, value,this.tipoValidacion._EMAIL)
      break;
      default:
        if(!Validate.value(value))
        {
          retorno = false;
          this.mensajeError = this.mensjaes.find(x=> x.tipo === this.tipoValidacion._EMPTY).mensaje;
        }
      break;

    }
    return retorno;
  }

  ProcesoValidaciÃ³n(expre, value, tipo)
  {
    let retorno = true;
    if(Validate.value(value))
    {
      let expresionReg = expre;
      if(!expresionReg.test(value))
      {        
        this.mensjaes.forEach((x)=>{
          if(x.tipo === tipo)
          {
            this.mensajeError = x.mensaje
          }
        })        
        retorno = false;
      }
    }else{      
      this.mensajeError = this.mensjaes.find(x=> x.tipo === this.tipoValidacion._EMPTY).mensaje;
      retorno = false;
    }
    return retorno;
  }

  InputValidacion(type:string, value:any, id:any)
  {
    console.log(value)     
    if(!this.Validacion(type,value.trim()))
    {
      this.formValidado = false;
      this.ChangeApariencia(id, true)
    }else{
      this.formValidado = true;
      this.ChangeApariencia(id, false)
    }
  }

  ChangeApariencia(id, show:boolean)
  {
    let parrafoMensaje = document.getElementById(id);
    if(show)
    {      
      parrafoMensaje.style.visibility = 'visible'
      parrafoMensaje.innerHTML = this.mensajeError;  
    }else{
      
      parrafoMensaje.style.visibility = 'hidden'
    }
    
  }

}
